import React from 'react';
import { CircularProgressbar, buildStyles } from 'react-circular-progressbar';
import './circularbar.css'
function CircularBar({percentage}){
    return (
        <div className='ProgressBar'>
            <div className='ProgressBar-t'>
                <span className='t-name'>Customers</span>
                <span className='ProgressBar-c'>Customers that buy product</span>
            </div>
            <div className='p-graph'>
                <div className='p-chart' style={{ width: '150px', position:'relative'}}>
                    <CircularProgressbar
                        value={percentage}
                        text={`${percentage}% Total New Customers`}
                        styles={buildStyles({
                        textColor: 'black', // Text color
                        pathColor: `rgb(69, 214, 95)`, // Progress bar color
                        trailColor: 'lightgray', // Background color
                        strokeWidth : 10,
                        textSize:10,
                        textStyle: {
                            fontSize: '14px',
                            textAnchor: 'middle', // Center the text horizontally
                            alignmentBaseline: 'middle', // Center the text vertically
                          },
                        })}
                    />
                </div>
            </div>
        </div>
    )
}
export default CircularBar;